.wrapper {
    display: flex;
    //justify-content: center;
    margin-bottom: 12px;
}

.video-player {
    margin-right: 20px;
}

.video {
    width: 340px;
    height: 200px;
    border: none;
}

.title {
    color: red;

}

.description-container {
    position: relative;
}

.description {
    white-space: pre-wrap;
}

.description-short { 
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.show-more-btn {
    background-color: white;
    color: red;
    cursor: pointer;
}

.like-section {
    margin-top: 4px;
    font-size: 2rem;
    
    .icon {
        margin-right: 6px;
        cursor: pointer;
    }
}

